"use client"

import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"
import { useTheme } from "@/components/theme-context"
import { BarChart, Bar, XAxis, YAxis, ResponsiveContainer, Tooltip } from "recharts"
import { Button } from "@/components/ui/button"
import { useState } from "react"

const data = [
  { name: "Mon", value1: 40, value2: 30 },
  { name: "Tue", value1: 50, value2: 35 },
  { name: "Wed", value1: 45, value2: 40 },
  { name: "Thu", value1: 60, value2: 45 },
  { name: "Fri", value1: 55, value2: 50 },
  { name: "Sat", value1: 70, value2: 55 },
  { name: "Sun", value1: 65, value2: 60 },
]

export function BarChartDemo() {
  const { mode } = useTheme()
  const isDark = mode === "dark"
  const [activeFilter, setActiveFilter] = useState("7D")

  return (
    <Card className="h-full flex flex-col">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className={`text-lg ${
            isDark ? "text-white" : "text-gray-900"
          }`}>
            Trade Volume
          </CardTitle>
          <div className="flex gap-1">
            {["1D", "7D", "30D"].map((filter) => (
              <Button
                key={filter}
                variant={activeFilter === filter ? "default" : "outline"}
                size="sm"
                onClick={() => setActiveFilter(filter)}
                className="h-7 px-2 text-xs"
              >
                {filter}
              </Button>
            ))}
          </div>
        </div>
      </CardHeader>
      <CardContent className="flex-1 flex flex-col">
        <ResponsiveContainer width="100%" height="100%" className="min-h-[200px]">
          <BarChart data={data}>
            <XAxis
              dataKey="name"
              tick={{ 
                fill: isDark ? "rgba(255,255,255,0.6)" : "rgba(0,0,0,0.6)", 
                fontSize: 12 
              }}
              axisLine={{ 
                stroke: isDark ? "rgba(255,255,255,0.1)" : "rgba(0,0,0,0.1)" 
              }}
            />
            <YAxis
              tick={{ 
                fill: isDark ? "rgba(255,255,255,0.6)" : "rgba(0,0,0,0.6)", 
                fontSize: 12 
              }}
              axisLine={{ 
                stroke: isDark ? "rgba(255,255,255,0.1)" : "rgba(0,0,0,0.1)" 
              }}
            />
            <Tooltip
              contentStyle={{
                backgroundColor: isDark 
                  ? "rgba(30, 41, 59, 0.95)" 
                  : "rgba(255, 255, 255, 0.95)",
                border: isDark 
                  ? "1px solid rgba(255,255,255,0.1)" 
                  : "1px solid rgba(0,0,0,0.1)",
                borderRadius: "8px",
                color: isDark ? "white" : "black",
              }}
            />
            <Bar
              dataKey="value1"
              fill="var(--color-primary)"
              radius={[4, 4, 0, 0]}
            />
            <Bar
              dataKey="value2"
              fill="var(--color-complementary)"
              radius={[4, 4, 0, 0]}
            />
          </BarChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  )
}
